//  Vue
import Vue from 'vue';
//  vee-validate -  Function
import {
  ValidationObserver,
  ValidationProvider,
  extend,
  localize,
  configure,
} from 'vee-validate';
//   vue-i18n zh_TW
import zhTW from 'vee-validate/dist/locale/zh_TW.json';
// 
import * as rules from 'vee-validate/dist/rules';

//  ValidationProvider // 
Vue.component('ValidationProvider', ValidationProvider);
//  ValidationObserver //  ValidationProvider 
Vue.component('ValidationObserver', ValidationObserver);

//  rules 
// https://logaretm.github.io/vee-validate/guide/rules.html#installing-all-rules
Object.keys(rules).forEach((rule) => {
  extend(rule, rules[rule]);
});

// 
// https://logaretm.github.io/vee-validate/guide/localization.html#using-the-default-i18n
localize('zh_TW', zhTW);

// Class
// https://logaretm.github.io/vee-validate/guide/state.html#css-classes
configure({
  classes: {
    valid: 'is-valid',
    invalid: 'is-invalid',
  },
});


